import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import WorkoutGraph from '../../components/workout/graph';
import WorkoutTable from '../../components/workout/table';
// import DogStatus from '../../components/dog/status';
import InviteFriend from '../../components/user/invite';
import UserUpdate from '../../components/user/updateSettings';
import Install from '../../components/install';
import Races from '../../components/races';
import Notifications from '../../components/notifications';
import WorkoutTotals from '../../components/workout/totals';
import WorkoutStatus from '../../components/workout/status';
import Strava from '../../components/strava';
import Garmin from '../../components/garmin';
import Teams from '../../components/team/teams';
import TeamPublicFeed from '../../components/team/publicFeed';
import StoryAdd from '../../components/story/add';
import StoryPublicFeed from '../../components/story/publicFeed';
import Ad from '../../components/ad/';
import YrWeather from '../../components/yrweather/';

const initialState = {
	isLoading: false,
	submenu: 'lastWorkouts',
	viewmenu: 'wall',
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
		this.setState({ isLoading: true });
        await appState.getFingerprint();
        await userStore.getInfo();
		await userStore.getNotifications();

		const { user } = userStore;
		if (user.currentLocationInfo && user.currentLocationInfo.coords) {
			const { latitude: lat, longitude: lon, altitude } = user.currentLocationInfo.coords;
			userStore.getWeatherUser({ lat, lon, altitude });
		}

		// await this.loadFeed();
		await this.loadWorkoutSummary();
		// await this.loadDogs();
		await Promise.all([
			this.loadWorkoutPlans(),
			this.loadTeams(),
		]);

		this.setState({ isLoading: false });
    }

	loadTeamInfo = async () => {
		await this.loadWorkoutSummary();
		await this.loadWorkoutPlans();
		// await this.loadTeams();
	}

	async loadTeams(props = this.props) {
		const { teamStore, workoutStore } = props.stores;
		const { currentTeam } = workoutStore;
		await teamStore.load({ query: { team: currentTeam, skipMusherWorkoutSummary: true }, addData: ['workoutSummary'] });
	}

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed({});
    }

	async loadWorkoutSummary() {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		// await workoutStore.load({ query: { team: currentTeam } });
        await userStore.getWorkoutSummary({ team: currentTeam });
    }

	loadWorkoutPlans = async (props = this.props) => {
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, workoutStore, userStore } = props.stores;
		const { team } = userStore;
		const { currentTeam = team.id } = workoutStore;

		if (currentTeam) {
			await workoutPlanStore.load({ query: {
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			} });
			const { workoutPlans } = workoutPlanStore;
			const workoutPlanCurrent = workoutPlans[0] || {};
			if (workoutPlanCurrent.id) {
				await workoutPlanStore.load(workoutPlanCurrent.id);
				workoutPlanStore.prepareWeekGraphs({ year });
			}
		} else {
			workoutPlanStore.updateItem('workoutPlans', []);
			workoutPlanStore.updateItem('workoutPlan', {});
			workoutPlanStore.resetGraphPlanWeekDistanceCurrent();
		}
	}

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	reloadFeeds = async (e) => {
		e.preventDefault();
		this.setState({ isLoading: true });
		await this.loadStoriesPublicFeed();
		await this.loadTeamsPublicFeed();
		this.setState({ isLoading: false });
	}

	async loadStoriesPublicFeed(props = this.props) {
		const { storyStore } = props.stores;
		await storyStore.loadPublicFeed({});
	}

	async loadTeamsPublicFeed(props = this.props) {
		const { teamStore } = props.stores;
		await teamStore.loadPublicFeed();
	}

	setViewMenu = (e) => {
		e.preventDefault();
		const menu = e.target.closest('a').dataset.menu;
		this.setState({ viewmenu: menu });
	}

	toggleViewmode = (e) => {
        const { appState } = this.props.stores;
		let mode = e.target.dataset.mode;
		if (!mode && e.target.parentNode) {
			mode = e.target.parentNode.dataset.mode;
		}
		appState.toggleViewmode(mode);
	}

	chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { isLoading, submenu, viewmenu } = this.state;
		const { userStore, dogStore, appState, workoutPlanStore, teamStore, workoutStore,
			stravaActivityStore, garminActivityDetailsStore } = this.props.stores;
		const { workoutPlan, workoutPlans } = workoutPlanStore;
		const { darkmode, viewmode } = appState;
		const {
			workouts,
			teams, tracks, isAdmin, dogs, user,
		} = userStore;
		const { yrWeather, currentLocationGeocode = {} } = user;
		const { formatted_address: place } = currentLocationGeocode;
		// const { dogs } = dogStore;
		// const dogsInjured = dogStore.findDogsByStatus(2);
		// const dogsPregnant = dogStore.findDogsByStatus(4);
		// const dogsMedication = dogStore.findDogsByStatus(8);
		// const dogsSick = dogStore.findDogsByStatus(9);
		// const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		// const dogsWithNeeds = dogStore.findDogsWithNeeds();

		const totalTeams = teams.length;
		const totalDogs = dogs.length;
		const totalTracks = tracks.length;

		const { currentTeam, cordovaActiveTrackingParams, isRunning, totalDistance, currentSpeed, totalTime } = workoutStore;

		const currentTeamObject = userStore.findTeam(currentTeam, true);
		const { workoutSummary } = teamStore;
		const workoutSummary2019 = (currentTeam ? teamStore.findWorkoutSummary(currentTeam, 2019) : teamStore.findWorkoutSummaryTotal(2019)) || {};
        const workoutSummary2020 = (currentTeam ? teamStore.findWorkoutSummary(currentTeam, 2020) : teamStore.findWorkoutSummaryTotal(2020)) || {};

		const { stravaActivities } = stravaActivityStore;
		const { garminActivityDetails } = garminActivityDetailsStore;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					{isAdmin && <div class={`col text-center subtopmenu`}>
						<a href='/admin' class='text-secondary stretched-link'><nobr><i class='fas fa-user-shield' /> <Text id='home.admin'>Admin</Text></nobr></a>
					</div>}
					<div class={`col text-center subtopmenu border-bottom border-primary`}>
						<a href='/' class='text-primary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='home.dashboard'>Dashboard</Text></nobr></a>
					</div>
					<div class={`col text-center subtopmenu`}>
						<a href='/feed' class='text-secondary stretched-link'><nobr><i class='fas fa-rss' /> <Text id='home.feed'>Feed</Text></nobr></a>
					</div>
					<div class={`col text-center subtopmenu`}>
						<a href='/todo' class='text-secondary stretched-link'><nobr><i class='fas fa-list' /> <Text id='home.todo'>Todo</Text></nobr></a>
					</div>
				</div>

				{isRunning && cordovaActiveTrackingParams && <>
					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom bg-light py-4'>
							<h1 class='text-success font-weight-bold'>
								Tracking
								<div class='spinner-grow ml-3' role='status' />
							</h1>
							<div class='row mb-3'>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.secToHms(totalTime)}</div>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.format(currentSpeed * 3.6, 1)} km/t</div>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.format(totalDistance / 1000, 2)} km</div>
							</div>
							<a class='btn btn-block btn-danger' href='/workouts/tracking'><Text id='workouts.go-to-tracking'>Go to tracking</Text></a>
							{/* {JSON.stringify(cordovaActiveTrackingParams)}
							{JSON.stringify(isRunning)} */}
						</div>
					</div>
				</>}

				{['beta'].indexOf(viewmode) > -1 ? <>
					<Ad stores={this.props.stores} url={this.props.url} />
					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-center'>
							<button
								type='button' 
								class='btn btn-link btn-sm font-weight-lighter'
								data-mode='normal'
								onClick={this.toggleViewmode}
							>
								<small>
									<i class='fas fa-flask' /> <Text id='header.viewmode-back-to-normal'>Gå tilbake til vanlig versjon</Text>
								</small>
							</button>
						</div>
					</div>

					<div class='bg-light'>
						<YrWeather stores={this.props.stores} data={yrWeather} place={place} />
					</div>

					<StoryAdd stores={this.props.stores} />

					<div class='row bg-light'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
							<small>
								<small>
									<div class='float-right'>
										{isLoading ? <>
											<div class='spinner-border text-primary' role='status'>
												<span class='sr-only'>Loading...</span>
											</div>
										</> : <>
											<a href='#' style='font-size: 1.8em;' onClick={this.reloadFeeds}><i class='fas fa-sync-alt' /></a>
										</>}
									</div>

									<ul class='nav nav-sm nav-tabs'>
										<li class='nav-item'>
											<a class={`nav-link ${viewmenu === 'wall' ? 'active' : ''}`} href='#' onClick={this.setViewMenu} data-menu='wall'>
												<i class='fas fa-rss' /> <Text id='home.wall'>Veggen</Text>
											</a>
										</li>
										<li class='nav-item'>
											<a class={`nav-link ${viewmenu === 'workout' ? 'active' : ''}`} href='#' onClick={this.setViewMenu} data-menu='workout'>
												<i class='fas fa-running' /> <Text id='home.workouts'>Treninger</Text>
											</a>
										</li>
										<li class='nav-item'>
											<a class={`nav-link ${viewmenu === 'totals' ? 'active' : ''}`} href='#' onClick={this.setViewMenu} data-menu='totals'>
												<i class='fas fa-list-ol' /> <Text id='home.totals'>Totaler</Text>
											</a>
										</li>
									</ul>
								</small>
							</small>
						</div>
					</div>

					{viewmenu === 'wall' && <>
						<StoryPublicFeed stores={this.props.stores} />
					</>}
					{viewmenu === 'workout' && <>
						<TeamPublicFeed stores={this.props.stores} />
					</>}
					{viewmenu === 'totals' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadTeamInfo} key={`teams-${currentTeam}`} />
							<WorkoutTotals stores={this.props.stores} key={`totals-${workoutSummary2020.distancekm}`} currentSeason={workoutSummary2020} prevSeason={workoutSummary2019} />
							<WorkoutStatus stores={this.props.stores} key={`status-${workoutSummary2020.distancekm}`} currentSeason={workoutSummary2020} prevSeason={workoutSummary2019} />
						</div>
					</>}
				</> : <>
					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadTeamInfo} key={`teams-${currentTeam}`} />
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							{/* {isLoading && <div class='text-center mt-5 mb-5'>
								<div class='spinner-border text-primary' role='status'>
									<span class='sr-only'>Loading...</span>
								</div>
							</div>} */}

							{!isLoading && workoutSummary && workoutSummary.length === 0 && (
								<div class='mb-5 py-3 border-top border-bottom'>
									<div class='text-center'>
										<div class='display-4'>
											<i class='fas fa-paw' />
										</div>
										<h5>
											<Text id='home.welcome-title'>Velkommen til The Musher.</Text>
										</h5>
										<p>
											<Text id='home.welcome-body-1'>The Musher er en app som er kontinuerlig under utvikling.
											Alle ideer/innspill/kommentarer mottas med stor takk på Facebooksiden vår 😃</Text>
										</p>
										<p>
											<Text id='home.welcome-body-2'>Fokus til appen er enkel registrering av hunder, team, treninger og hendelser rundt hundene.
											Når man har mange hunder så er det ofte vanskelig å holde styr på løpetid, vaksiner, treninger,
											skader, vetrinærbesøk, chiplister, osv.</Text>
										</p>
										<p>
											<Text id='home.welcome-body-3'>Alle data lagres sentralt i skyen hos AWS (Amazon Web Services) og det vil derfor være mulig å bruke appen fra flere telefoner
											og datamaskiner samtidig. Dette kan være fint dersom det er flere som trener og følger opp hundene.</Text>
										</p>
									</div>

									<h5>
										<Text id='home.howto-title'>Slik kommer du i gang:</Text>
									</h5>
									<ol>
										{totalTeams < 1 && <li><a class='btn btn-info mt-3' href='/teams/'><Text id='home.howto-step-1'>Legg inn teamene dine.</Text></a></li>}
										{totalDogs < 1 && <li><a class='btn btn-info mt-3' href='/dogs/'><Text id='home.howto-step-2'>Legg inn hundene dine.</Text></a></li>}
										{totalTracks < 1 && <li><a class='btn btn-info mt-3' href='/tracks/'><Text id='home.howto-step-3'>Legg inn rundene dine.</Text></a></li>}
										<li><a class='btn btn-info mt-3' href='/workouts/'><Text id='home.howto-step-4'>Begynn å registrere treningene.</Text></a></li>
									</ol>
								</div>
							)}

							<WorkoutGraph stores={this.props.stores} isLoading={isLoading} key={`graph-${workoutPlan.id}-${workoutPlans.length}`} />
							{['beta', 'advanced'].indexOf(viewmode) > -1 && <>
								<WorkoutTable
									stores={this.props.stores}
									isLoading={isLoading}
									key={`workout-table-${workoutPlan.id}-${workoutPlans.length}`}
									showHeader={false}
								/>
							</>}

						</div>
					</div>				

					<Ad stores={this.props.stores} url={this.props.url} />

					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
							<div class='row'>

								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${submenu === 'races' ? 'btn-danger' : 'btn-outline-danger'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='races'
										onClick={this.chooseSubmenu}
									><i class='fas fa-dog' /> <Text id='home.races'>Løp</Text></button>
								</div>

								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${submenu === 'lastWorkouts' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='lastWorkouts'
										onClick={this.chooseSubmenu}
									><i class='fas fa-running' /> <Text id='home.workouts'>Treninger</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${submenu === 'totals' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='totals'
										onClick={this.chooseSubmenu}
									><i class='fas fa-equals' /> <Text id='home.totals'>Totaler</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${submenu === 'workoutplanStatus' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='workoutplanStatus'
										onClick={this.chooseSubmenu}
									><i class='fas fa-chart-line' /> <Text id='home.status'>Status</Text></button>
								</div>
							</div>
						</div>
						{submenu === 'races' && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<Races stores={this.props.stores} />
						</div>}
						{submenu === 'lastWorkouts' && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<Install />
							<InviteFriend stores={this.props.stores} />
						</div>}
						{submenu === 'totals' && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<WorkoutTotals stores={this.props.stores} key={`totals-${workoutSummary2020.distancekm}`} currentSeason={workoutSummary2020} prevSeason={workoutSummary2019} />
						</div>}
						{submenu === 'workoutplanStatus' && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<WorkoutStatus stores={this.props.stores} key={`status-${workoutSummary2020.distancekm}`} currentSeason={workoutSummary2020} prevSeason={workoutSummary2019} />
						</div>}

					</div>

					<Notifications stores={this.props.stores} />

					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
							<button
								type='button' 
								class='btn btn-success btn-block'
								data-mode='beta'
								onClick={this.toggleViewmode}
							>
								<i class='fas fa-flask' /> <Text id='header.viewmode-try-beta'>Prøv den nye versjonen av forsiden</Text>
							</button>
						</div>
					</div>

					{submenu === 'lastWorkouts' && <div class='row'>

						{user && user.strava && stravaActivities && stravaActivities.length > 0 && (
							<div
								class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-top border-bottom pt-3 pb-3 my-2 overflow-auto'
								style='max-height: 30vh;'
							>
								<h5><Text id='workout.index.strava-workouts'>Strava workouts / not imported</Text></h5>
								{stravaActivities && stravaActivities.map(act =>
									<Strava stores={this.props.stores} activity={act} key={`strava-${currentTeam}-${act.id}`} />)}
							</div>
						)}
						{user && user.garmin && garminActivityDetails && garminActivityDetails.length > 0 && (
							<div
								class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-top border-bottom pt-3 pb-3 my-2 overflow-auto'
								style='max-height: 30vh;'
							>
								<h5><Text id='workout.index.garmin-workouts'>Garmin workouts / not imported</Text></h5>
								{garminActivityDetails && garminActivityDetails.map(act =>
									<Garmin stores={this.props.stores} activity={act} key={`garmin-${currentTeam}-${act.id}`} />)}
							</div>
						)}

						{workouts && workouts.length > 0 && <div class='col-12 mb-4'>
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>		
								<h5>
									<Text id='home.last-workouts' fields={{ total: workouts.length }}>Siste treningsturer - {workouts.length} stk</Text>
									{currentTeamObject && currentTeamObject.name && <div>
										<small class='font-weight-lighter'>(<Text id='workout.index.filtered-by'>Filtered by</Text>: {currentTeamObject.name})</small>
									</div>}
								</h5>
							</div>
							{workouts && workouts.map(workout => (
								<Workout stores={this.props.stores} workout={workout} />
							))}
						</div>}
					</div>}

				</>}

				{/* {!isLoading && <UserUpdate stores={this.props.stores} />} */}

			</div>
		);
	}
}

export default Home;
